@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600&family=Raleway:wght@200;300;400;500&display=swap");
/* font-family: 'Raleway', sans-serif; */
/* font-family: 'Orbitron', sans-serif; */

:root {
  /* colors */
  --color-bg: #111;
  --color-dimmed: #555;
  --color-light: #eee;
  --color-highlight: #9dff00;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
}

html {
  background-color: var(--color-bg);
}

body {
  padding: 2.6rem 0;
  font-family: "Raleway", sans-serif;
  line-height: 1;
}

@media only screen and (min-width: 768px) {
  body {
    padding: 2rem;
  }
}
